import React, { Component } from "react";
import "./registr.css";
import axios from "../../../server/node_modules/axios";
import {Redirect} from 'react-router-dom'; 

class Registr extends Component {
    state = {email : "" , password : "", RedirectToHome : false, isError : false}
// ____

registr = () => {
        this.setState({isError : false});
        
        axios.post('/users/registr',{
            email : this.state.email,
            password : this.state.password
        }).then(res=>{
           
            if(res.status === 201){
                this.setState({RedirectToHome : true});
                this.props.setUser({email: this.state.email , password: this.state.password});
            }
            else{
                this.setState({isError : true})
                console.log(`error code : ${res.status}`);
                
            }
            
        }).catch(err=>{
            this.setState({isError : true})
            console.log(err);
        })
    }
// ____

    render(){
            const disabled = !this.state.email || !this.state.password;
        if(this.state.RedirectToHome){
            return <Redirect to = '/'/>
        }

        return(
            <div className='registr-User'>
                <h1>Registr Page</h1>
                <input type='email' onChange={evt => this.setState({email : evt.target.value})} 
                required='required' placeholder='email'/>
                <br/>
                <input type="password" onChange={evt => this.setState({password : evt.target.value})}
                 required='required' placeholder='password'/>
                 <br/>
               {this.state.isError ? <p style={{color: 'red'}}>Registr Error</p> : ""}
                <button disabled={disabled} onClick={this.registr}>Registr</button>
            </div>
        )
    }
}
export default Registr;
