import React, { Component } from 'react';
import axios from '../../../server/node_modules/axios';
import SearchArea from './search_Item_results';
import MovieList from './movie_list_Results';
import './home.css';



const baseImgUrl = 'https://image.tmdb.org/t/p/w500';
let myKey = 'd759a614b16c0c1c0295b4313e94aeec';

class Home extends Component {

    constructor(props) {
        super(props);
        this.state = {
            moviesTop: [],
            movies: [],
            isLoaded: false,
            moviesPopular: [],
            tvShowsTop: [],
            searchTerm: '',
            tvShowsPopular: [],
            totalResuolts : 0,
            currentPage:1
        }
        console.log("constructor");
    }
// ______________________________________________
    async componentDidMount() {
        const response = await axios.get("http://localhost:8080/home");
        const moviesData = response.data.movies;
        console.log(moviesData);
        console.log("componentDidMount");
        this.setState({moviesTop: moviesData});
    }
// __________________________________________

    handleSubmit = (e) => {
        e.preventDefault();
        fetch(`https://api.themoviedb.org/3/search/movie?api_key=${myKey}&query=${this.state.searchTerm}`)
            .then(data => data.json())
            .then(data => {
                console.log(data)
                this.setState({ movies: [...data.results], totalResuolts:data.total_results });
                console.log("search is done");
            })
    }
// _________________________________
    handleChange = (e) => {
        this.setState({ searchTerm: e.target.value })
    }
// _________________________________
    // nextPage = (pageNumber)=>{
    //     fetch(`https://api.themoviedb.org/3/search/movie?api_key=${myKey}&query=${this.state.searchTerm}&page=${pageNumber}`)
    //     .then(data => data.json())
    //     .then(data => {
    //         console.log(data)
    //         this.setState({ movies: [...data.results],currentPage:pageNumber });
    //         console.log("loki");
    //     })
    // }


    render() {

        const allmovies = this.state.moviesTop.map((movie, index) =>
            <div className="card movie-card">
                <img className="card-img-top" src={`${baseImgUrl}/${movie.poster_path}`} alt={"a"} />
                <div className="card-body">
                    
                <h4 className="card-title" key={index}>{movie.title}</h4>
                <p className="card-text">{movie.overview}</p>
                <p className="card-text">{movie.vote_average}</p>
                {console.log("render")}
               
                </div>
            </div>)
        // const numberPages = Math.floor(this.state.totalResuolts / 20); 
        return (
            <div className='Home '>
                <h1>Welcome To Foxx Movie</h1>
                <div className="container">
                <SearchArea handleSubmit={this.handleSubmit} handleChange={this.handleChange}/>
                <MovieList movies={this.state.movies}/>
                
                </div>
                {allmovies}
               
                
                
            </div>
        )
    }
}

export default Home;