import React from 'react';
const Movie = (props) => {
    return (
        <section>

            
                <div className="card movie-card" >

                    {props.myImage = <img className="card-img-top" src={`https://image.tmdb.org/t/p/w500${props.myImage}`} /> }

                    {props.myTitle = <h6 className="card-title">{props.myTitle}</h6>}
                    {props.myOverview = <p className="card-text">{props.myOverview}</p>}

                </div>

           
        </section>

    )
}

export default Movie;